import React from 'react'
import TextField from '@material-ui/core/TextField'
import Box from '@material-ui/core/Box'
import Fab from '@material-ui/core/Fab'
import Choice from './Choice'
import AddIcon from '@material-ui/icons/AddBox'
import { makeStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'
import {STORYLET_UPDATE_FIELD} from '../../../redux/actionTypes'
import {actionCreateChoice} from '../../../redux/actionCreators/createTaleActions'

const useStyles = makeStyles(theme => ({
    textField: {
        display: "flex",
        marginTop: theme.spacing(2)
    },
    box: {
        border: "1px solid #bbb",
        borderRadius: 2,
        position: "relative"
    },
    joiner: {
        position: "absolute",
        height: theme.spacing(5),
        width: 1,
        background: "#bbb",
        left: "50%",
        top: -(theme.spacing(5)+1)
    },
    choiceContainer: {
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-between"
    },
    addChoiceBtn: {
        alignSelf: "center",
        height: 90,
        borderRadius: 6,
        margin: `0 ${theme.spacing(1)}px`,
        textAlign: "center",
        fontSize: 16,
        lineHeight:" 20px",
        '& span': {
            display: "block"
        },
        '& svg': {
            fontSize: 36
        }
    }
}))

const Storylet = props => {
    const classes = useStyles()
    return (
        <div>
            {/* STORYLET BOX -- STARTS HERE */}
            <Box className={classes.box} px={1} pb={2}>
                <form noValidate autoComplete="off">
                    <TextField 
                        label="Storylet title" 
                        variant="outlined" 
                        className={classes.textField}
                        value={props.st.title}
                        onChange={e => props.typingStValue(props.st.id, "title", e.target.value)}
                    />
                    <TextField
                        label="Storylet body"
                        multiline
                        rows="6"
                        variant="outlined"
                        className={classes.textField}
                        value={props.st.text}
                        onChange={e => props.typingStValue(props.st.id, "text", e.target.value)}
                    />
                </form>
            </Box>
            {/* STORYLET BOX -- ENDS HERE */}

            {/* CHOICES BOX -- STARTS HERE */}
            <Box className={classes.box} p={2} my={5}>
                <span className={classes.joiner}></span>
                <div className={classes.choiceContainer}>
                    {
                        props.st.choices && props.st.choices.map(cID => (
                            <Choice 
                                c={props.choices[cID]} 
                                stID={props.st.id}
                                sbI={props.sbI}
                                isSelected={props.sbItem.selectedCID === cID} 
                                key={cID} 
                            />
                        ))
                    }
                    <Fab className={classes.addChoiceBtn} variant="extended" color="primary" onClick={() => props.createAChoice(props.st.id)}>
                        <AddIcon />
                        <span className="txt">Add Choice</span>                        
                    </Fab>
                </div>
                
            </Box>
            {/* CHOICES BOX -- ENDS HERE */}

        </div>
    )
}

const UpdateStField = (stID, fieldName, value, dispatch) => {
    dispatch({
        type: STORYLET_UPDATE_FIELD,
        payload: {
            stID, fieldName, value
        }
    })
}
const mapStateToProps = state => ({
    choices: state.wipTale.choices
})
const mapDispatchToProps = dispatch => ({
    typingStValue: (stID, fieldName, value) => UpdateStField(stID, fieldName, value, dispatch),
    createAChoice: stID => dispatch(actionCreateChoice(stID))
})
export default connect(mapStateToProps, mapDispatchToProps)(Storylet)