import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Box from '@material-ui/core/Box'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'
import AddBoxIcon from '@material-ui/icons/AddBox'
import GamepadIcon from '@material-ui/icons/Gamepad'
import DeleteIcon from '@material-ui/icons/Delete'
import SaveIcon from '@material-ui/icons/Save'
import TouchAppIcon from '@material-ui/icons/TouchApp'
import GavelIcon from '@material-ui/icons/Gavel'
import { red } from '@material-ui/core/colors'

const useStyles = makeStyles(theme => ({
    panel: {
        width: 40,
        backgroundImage: `linear-gradient(${theme.palette.primary.main}, ${theme.palette.primary.dark})`,
        position: 'absolute',
        left: 0,
        top: 50,
        height: "calc(100vh - 60px)",
        overflow: "auto"
    },
    btnPane: {
        color: '#fff',
        padding: `${theme.spacing(2)}px 0`,
        '& li': {
            textAlign: 'center',
            padding: "7px 0 3px 0"
        },
        '& li+li': {
            borderTop: '1px solid #fff'
        },
        '& li:active': {
            backgroundColor: red.A400
        },
        '& li svg': {
            fontSize: 27
        }
    }
}))

const ControlPanel = props => {
    const classes = useStyles()
    const { history, deleteComponent, paintFlowboard, toggleJStick } = props
    const backToEdit = () => history.push('/edit-tale/current')
    return (
        <Box className={classes.panel}>
            <ul className={classes.btnPane}>
                <li title="Back to Editor" onClick={backToEdit}><ArrowBackIcon /></li>
                <li title="Add component"><AddBoxIcon /></li>
                <li title="Save all"><SaveIcon /></li>
                <li title="Select component"><TouchAppIcon /></li>
                <li title="Toggle joystick" onClick={toggleJStick}><GamepadIcon /></li>
                <li title="Auto format" onClick={paintFlowboard}><GavelIcon /></li>
                <li title="Increase draw area"><ZoomOutMapIcon /></li>
                <li title="Delete selected" onClick={deleteComponent}><DeleteIcon /></li>
            </ul>
        </Box>
    )
}

export default ControlPanel