import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import Box from '@material-ui/core/Box'
import {connect} from 'react-redux'
import Checkbox from '@material-ui/core/Checkbox';
import {
    CHOICE_UPDATE_TEXT, 
    STORYBOARD_SELECT_CHOICE,
    STORYBOARD_INSERT_AT
} from '../../../redux/actionTypes'

const useStyles = makeStyles(theme => ({
    choiceBox: {
        position: "relative",
        margin: theme.spacing(1),
        flexGrow: 1
    },
    selectChoice: {
        position: "absolute",
        top: -theme.spacing(1),
        right: -theme.spacing(1)
    },
    choiceText: {
        width: "100%"
    }
}))

const updateChoiceText = (dispatch, cID, value) => {
    dispatch({
        type: CHOICE_UPDATE_TEXT,
        payload: {cID, value}
    })
}

const selectChoice = (dispatch, stID, cID, isSelected, sbI, nextStID) => {
    cID = isSelected ? cID : '' // blank if Deselcetd

    // Select The Choice
    dispatch({
        type: STORYBOARD_SELECT_CHOICE,
        payload: {sbI, cID}
    })

    // Update the storyboard
    // Rollback upto this sbI (storyboard Index)
    // And Insert nextStID
    // Optionally pass isRemoveOnly for Deselection
    dispatch({
        type: STORYBOARD_INSERT_AT,
        payload: { sbI, nextStID, isRemoveOnly: !isSelected }
    })
}

const Choice = props => {
    const classes = useStyles()
    return (
        <Box className={classes.choiceBox}>
            <TextField
                label="Choice text"
                multiline
                rows="3"
                variant="outlined"
                className={classes.choiceText}
                value={props.c.text}
                onChange={e => props.typingChoiceText(props.c.id, e.target.value)}
            />
            <Checkbox
                checked={props.isSelected}
                onChange={(e) => props.handleSelection(props.stID, props.c.id, e.target.checked, props.sbI, props.c.next)}
                value="primary"
                className={classes.selectChoice}
                inputProps={{ 'aria-label': 'primary checkbox' }}
            />
        </Box>
    )
}
const mapDispatchToProps = dispatch => ({
    typingChoiceText: (cID, value) => updateChoiceText(dispatch, cID, value),
    handleSelection: (stID, cID, isSelected, sbI, nextStID) => selectChoice(dispatch, stID, cID, isSelected, sbI, nextStID)
})
export default connect(null, mapDispatchToProps)(Choice)